@tailwind base;
@tailwind components;
@tailwind utilities;
body::-webkit-scrollbar {
  width: 10px;
}
body::-webkit-scrollbar-track {
  background-color: transparent;
}
body::-webkit-scrollbar-thumb {
  background: #909090;
  border-radius: 20px;
}
@media (min-width: 1000px) {
  .main-container-page-home {
    background-image: url('https://images.unsplash.com/photo-1627389955928-2f3a48686106?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1374&q=80');
    background-repeat: no-repeat;
    background-size: cover;
    animation-duration: 3s;
    animation-name: zoomin;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    -webkit-animation-duration: 3s;
    -webkit-animation-name: zoomin;
  }
}
.main-container-page-home {
  background-image: url('https://images.unsplash.com/photo-1627389955928-2f3a48686106?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1374&q=80');
  background-repeat: no-repeat;
  background-size: cover;
}

@keyframes zoomin {
  0% {
    background-size: 100%;
  }
  50% {
    background-size: 102%;
  }
  100% {
    background-size: 100%;
  }
  /* from {
    background-size: 100%;
  }
  to {
    background-size: 110%;
  }
  to {
    background-size: 100%;
  } */
}

@-webkit-keyframes zoomin {
  from {
    background-size: 100%;
  }
  to {
    background-size: 110%;
  }
}
.information-cards {
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}
.information-cards:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  background-color: #252525;
  color: #ffffff;
}
.contact-us-btn {
  transition: transform 0.5s;
}
.contact-us-btn:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  transform: translateY(-5px);
}
.catchphrase-sentence {
  animation-name: fade-in;
  animation-duration: 7s;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
